import React, { useState, useRef, useEffect } from "react";
import Keyboard from "react-simple-keyboard";
import "react-simple-keyboard/build/css/index.css";

export default function Keyboards(props) {
    const { options, setOptionsKeyboard, optionsKeyboard } = props;
    const [layoutName, setLayoutName] = useState("default");
    const keyboard = useRef();

    const { Nombre, RFC, Correo, Celular, Telefono, Calle, Numero, Colonia, Campo, Kilo, ComentariosP, NombreNuevo, 
        CorreoNuevo, RFCNuevo, CelularNuevo, TelefonoNuevo, ColoniaNuevo, NumeroNuevo, CalleNuevo, 
        CodigoPNuevo, Modelo, Color, Anio, VINDatos, Placas, Kilometraje, CodigoP} = optionsKeyboard.value
    //console.log("Tengo las opciones en el KEYBOARDS: ", Nombre, RFC, Correo, Celular, Telefono, Calle, Numero, Colonia )
    useEffect(() => {
        //El input de confirmación no tendrá registro de entrada.
        keyboard.current.setInput(Campo, "Campo");
        keyboard.current.setInput(Kilo, "Kilo");
        keyboard.current.setInput(ComentariosP, "ComentariosP");

        keyboard.current.setInput(Nombre, "Nombre");
        keyboard.current.setInput(RFC, "RFC");
        keyboard.current.setInput(Correo, "Correo");
        keyboard.current.setInput(Celular, "Celular");
        keyboard.current.setInput(Telefono, "Telefono");
        keyboard.current.setInput(Calle, "Calle");
        keyboard.current.setInput(Numero, "Numero");
        keyboard.current.setInput(Colonia, "Colonia");
        keyboard.current.setInput(CodigoP, "CodigoP");

        keyboard.current.setInput(NombreNuevo, "NombreNuevo");
        keyboard.current.setInput(CorreoNuevo, "CorreoNuevo");
        keyboard.current.setInput(RFCNuevo, "RFCNuevo");
        keyboard.current.setInput(CelularNuevo, "CelularNuevo");
        keyboard.current.setInput(TelefonoNuevo, "TelefonoNuevo");
        keyboard.current.setInput(ColoniaNuevo, "ColoniaNuevo");
        keyboard.current.setInput(NumeroNuevo, "NumeroNuevo");
        keyboard.current.setInput(CalleNuevo, "CalleNuevo");
        keyboard.current.setInput(CodigoPNuevo, "CodigoPNuevo");

        keyboard.current.setInput(Modelo, "Modelo");
        keyboard.current.setInput(Color, "Color");
        keyboard.current.setInput(Anio, "Anio");
        keyboard.current.setInput(VINDatos, "VINDatos");
        keyboard.current.setInput(Placas, "Placas");
        keyboard.current.setInput(Kilometraje, "Kilometraje");
    },[])
    
    const onChangeAll = inputs => {
        setOptionsKeyboard({ ...optionsKeyboard,
            value: inputs
        })
    };
    
    const handleShift = () => {
        const newLayoutName = layoutName === "default" ? "defaultMayus" : "default";
        //console.log("En handleShift: ", newLayoutName)
        setLayoutName(newLayoutName);
        setOptionsKeyboard({ ...optionsKeyboard,
            layoName: newLayoutName
        })
    };
    
    const onKeyPress = button => {
        //console.log("Button pressed", button);
    
        /**
         * If you want to handle the shift and caps lock buttons
         */
         if (button === "{number}") handleShift("phone")
         if (button === "{abc}") handleShift("default")
         if (button === "{shiftright}") handleShift("shift")
         if (button === "{vin}") handleShift("vin")
         if (button === "{clean}") handleClean()
    };

    const handleClean = () => {
        keyboard.current.clearInput();
        //document.getElementById("inputContacto").value = ""
        //setContacto(null)
    }

    return(
        <>
            <Keyboard
            keyboardRef={r => (keyboard.current = r)}
            inputName={options.inpName}
            layoutName={options.layoName}
            onChangeAll={onChangeAll}
            onKeyPress={onKeyPress}
            layout={{
                default: [
                    "@ .com .com.mx @gmail.com @hotmail.com {clean}",
                    "1 2 3 4 5 6 7 8 9 0 {backspace}",
                    "q w e r t y u i o p",
                    "{espacio} a s d f g h j k l ñ {espacio}",
                    "{espacio} z x c v b n m - _ {espacio}",
                    "{espacio} . {space} {shiftright} {espacio}",
                ],
                defaultMayus: [
                    "@ .com .com.mx @gmail.com @hotmail.com {clean}",
                    "1 2 3 4 5 6 7 8 9 0 {backspace}",
                    "Q W E R T Y U I O P",
                    "{espacio} A S D F G H J K L Ñ {espacio}",
                    "{espacio} Z X C V B N M - _ {espacio}",
                    "{espacio} . {space} {shiftright} {espacio}",
                ],
                vin: [
                    "{espacio} {espacio} {espacio} {espacio} {clean}",
                    "1 2 3 4 5 6 7 8 9 0 {backspace}",
                    "Q W E R T Y U I O P",
                    "{espacio} A S D F G H J K L {espacio}",
                    "{espacio} Z X C V B N M {espacio} {espacio}",
                    "{espacio} {space} {espacio}",
                ],

                phone: [
                    "{clean} {espacio} {backspace}",
                    "1 2 3",
                    "4 5 6",
                    "7 8 9",
                    "{espacio} 0 {espacio}"
                ],
            }}
            display={{
                "{backspace}": "⌫",
                "{clean}": "⇤ borrar",
                "{number}": "123..",
                "{abc}": "abc",
                "{shiftright}": "⇧ Mayús",
                "{ok}": "Buscar",
                "{espacio}": " ",
                "{space}": " ",
            }}
        />
        </>
    )
}